import { Link } from "react-router-dom";
import { FaBackward } from "react-icons/fa";
import { RiOrderPlayFill } from "react-icons/ri";
import React, { useEffect, useState } from "react";
import { createOrder } from "../actions/orderAction";
import { useDispatch, useSelector } from "react-redux";
import CheckOutSteps from "../components/CheckOutSteps";

const PlaceOrder = ({ history }) => {
  const user = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const cart = useSelector((state) => state.cart);

  if (user && user._id) {
    cart.itemsPrice = cart.cartItems
      .filter((x) => x.userId === user._id)
      .reduce((acc, item) => acc + item.price * item.qty, 0);
    cart.taxPrice = Number((0.15 * cart.itemsPrice).toFixed(2));
    cart.totalPrice = Number(cart.taxPrice + cart.itemsPrice).toFixed(2);
  }

  const orderCreate = useSelector((state) => state.order);
  const { order, success, error } = orderCreate;

  useEffect(() => {
    if (success) {
      history.push(`/order/${order._id}`);
    }
  }, [history, success]);


  const placeOrderHandler = () => {
    dispatch(
      createOrder({
        orderItems: cart.cartItems,
        totalItems: cart.cartItems.length,
        shippingAddress: cart.shippingAddress,
        paymentMethod: cart.paymentMethod,
        itemsPrice: cart.itemsPrice,
        taxPrice: cart.taxPrice,
        totalPrice: cart.totalPrice,
      })
    );
  };

  return (
    <section className=" bg-gray-50 text-gray-600 body-font relative">
      <div className="container px-5 py-10 mx-auto">
        <div className="">
          <Link to="/shipping">
            {" "}
            <FaBackward color="blue" />{" "}
          </Link>
        </div>
        <div className="flex flex-col items-center w-full mb-12">
          <h1 className="sm:text-3xl text-2xl  inline-flex  items-center font-medium title-font mb-4 text-gray-700">
            Place Order <RiOrderPlayFill className="mx-1" color="blue" />{" "}
          </h1>
        </div>
        <CheckOutSteps step1 step2 step3 step4 />

        <section className="text-gray-600 body-font">
          <div className="container px-5 py-10 mx-auto">
            <div className="flex flex-wrap -m-2">
              <div className="p-2 lg:w-full md:w-full w-full">
                <div className="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                  {user &&
                  user._id &&
                  cart.cartItems.filter((x) => x.userId === user._id).length ===
                    0 ? (
                    <p>You cart is empty </p>
                  ) : (
                    <>
                      {cart.cartItems
                        .filter((x) => x.userId === user._id)
                        .map((item, index) => (
                          <>
                            <img
                              className="w-16 h-16 bg-gray-100 object-cover object-center flex-shrink-0 rounded-full mr-4"
                              src={item.images[0].url}
                              alt={item.name}
                            />
                            <div className="flex-grow">
                              <h2 className="text-gray-900 title-font font-medium">
                                <Link to={`/product/${item.product}`}>
                                  {" "}
                                  {item.name}{" "}
                                </Link>
                              </h2>
                              <p className="text-gray-500">
                                {item.qty} x {item.price} PKR ={" "}
                                {item.qty * item.price} PKR
                              </p>
                            </div>
                          </>
                        ))}
                    </>
                  )}

                  <div className="flex-grow">
                    <strong> Address :</strong>
                    {cart.shippingAddress.address}, {cart.shippingAddress.city},{" "}
                    {cart.shippingAddress.postalCode},{"  "}
                    {cart.shippingAddress.country}
                    <br />
                    <strong> Phone Number :</strong>
                    {cart.shippingAddress.phoneNumber}
                    <br />
                    <strong> Method : </strong>
                    {cart.paymentMethod === "caseOnDelivery" && (
                      <>Cash On Delivery</>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section className="text-gray-600 body-font">
          <div className=" container px-5 mx-auto">
            <div className="flex flex-wrap -m-2">
              <div className=" ">

                <h2> Order Summary </h2>
                <div className="bg-gray-900 px-10 text-gray-100 h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <div className="flex-grow">
                  <strong>Price</strong>: {" "}{cart.itemsPrice} PKR
                  <br />
                  <strong>TAX</strong>:{" "}{cart.taxPrice} PKR
                  <br />
                  <strong>Total</strong>:{" "}{cart.totalPrice} PKR
                  <br />
                  {error && <p> {error} </p>}
                  
                    <button
                     className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-200 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                      disabled={cart.cartItems === 0}
                      onClick={placeOrderHandler}
                    >
                      Place Order
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </section>
  );
};

export default PlaceOrder;
